import {
	mapState
} from 'vuex';
import uniNumberBox from '@/components/uni-number-box.vue'
export default {
	components: {
		uniNumberBox
	},
	data() {
		return {
			total: 0.00, //总价格
			allChecked: false, //全选状态  true|false
			empty: false, //空白页现实  true|false
			cartList: [],
			selectedCartIds: ''
		};
	},
	onShow() {
		this.inquiryCart();
	},
	watch: {
		//显示空白页
		cartList(e) {
			let empty = e.length === 0 ? true : false;
			if (this.empty !== empty) {
				this.empty = empty;
			}
		}
	},
	computed: {
		...mapState(['hasLogin', 'userInfo'])
	},
	methods: {
		//请求数据
		// async loadData() {
		// 	let list = await this.$api.json('cartList');
		// 	let cartList = list.map(item => {
		// 		item.checked = true;
		// 		return item;
		// 	});
		// 	this.cartList = cartList;
		// 	this.calcTotal(); //计算总价
		// },
		//查询购物车商品
		inquiryCart() {
			let postData = {
				userUuid: this.userInfo.userUuid
			}
			this.$api.request.cartList(postData, res => {
				if (res.body.status.statusCode === '0') {
					this.cartList = res.body.data.carts;
					this.calcTotal(); //计算总价
				} else {
					console.log(res.body.status.errorDesc);
				}
			});
		},
		//监听image加载完成
		onImageLoad(key, index) {
			this.$set(this[key][index], 'loaded', 'loaded');
		},
		//监听image加载失败
		onImageError(key, index) {
			this[key][index].image = '/static/errorImage.jpg';
		},
		navToLogin() {
			uni.navigateTo({
				url: '/pages/public/login'
			})
		},
		//选中状态处理
		check(type, index) {
			if (type === 'item') {
				this.cartList[index].checked = !this.cartList[index].checked;
			} else {
				const checked = !this.allChecked
				const list = this.cartList;
				list.forEach(item => {
					item.checked = checked;
				})
				this.allChecked = checked;
			}
			this.calcTotal(type);
		},
		//修改购物车商品数量
		numberChange(data) {
			this.cartList[data.index].unit = data.number;
			this.calcTotal();
			//调用后台修改数量
			let postData = {
				shoppingCartUuid: this.cartList[data.index].shoppingCartUuid,
				unit: data.number
			}
			this.$api.request.setCartNum(postData, res => {
				if (res.body.status.statusCode === '0') {

				} else {
					console.log(res.body.status.errorDesc);
				}
			},true);
		},
		//删除
		deleteCartItem(index) {
			let list = this.cartList;
			let row = list[index];
			let id = row.shoppingCartUuid;
			this.cartList.splice(index, 1);
			this.calcTotal();
			//调用后台移除
			let postData = {
				shoppingCartUuid: id
			}
			this.$api.request.removeCart(postData, res => {
				if (res.body.status.statusCode === '0') {

				} else {
					console.log(res.body.status.errorDesc);
				}
			},true);
		},
		//清空
		clearCart() {
			uni.showModal({
				content: '清空购物车？',
				success: (e) => {
					if (e.confirm) {
						this.cartList = [];
						this.$api.request.clearCart({
							userUuid: this.userInfo.userUuid
						}, res => {
							if (res.body.status.statusCode === '0') {
								console.log('购物车商品已清除');
							} else {
								console.log(res.body.status.errorDesc);
							}
						},false);
					}
				}
			})
		},
		//计算总价
		calcTotal() {
			let list = this.cartList;
			if (list.length === 0) {
				this.empty = true;
				return;
			}
			let total = 0;
			let checked = true;
			let selectedCartIds = ''; 
			list.forEach(item => {
				if (item.checked === true) {
					if (item.productDTO.skuEnabled)
						total += item.productSkuDTO.skuUnitPrice * item.unit;
					else
						total += item.productDTO.unitPrice * item.unit;
					selectedCartIds = selectedCartIds + "," + item.shoppingCartUuid;
				} else if (checked === true) {
					checked = false;
				}
			})
			this.allChecked = checked;
			//商品总额
			this.total = total.toFixed(2); 
			//购物车中选择的商品
			if (selectedCartIds.substr(0, 1) == ',') {
				selectedCartIds = selectedCartIds.substr(1);
			}
			this.selectedCartIds = selectedCartIds;
		},
		//创建订单
		createOrder() {
			uni.navigateTo({
				url: '/pages/order/createOrder?data=' + JSON.stringify(this.selectedCartIds)
			})
		},
		//详情
		navToDetailPage(item){
			let id = item.productDTO.productUuid;
			uni.navigateTo({
				url: '/pages/product/product?id='+id
			})
		},
	}
}
